body {
  font-family: 'Georgia', serif;
  background-color: #f8f4e4; /* Sepia background for vintage feel */
  color: #333;
}

.newspaper-dashboard {
  display: grid;
  grid-template-rows: auto auto 1fr 1fr auto;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "header header"
    "chord-text chord-chart"
    "radar-chart scatter-chart"
    "text text"
    "line-chart line-chart"
    "footer footer";
  gap: 15px;
  width: 90%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

/* Header */
.header {
  grid-area: header;
  text-align: center;
  border-bottom: 2px solid #333;
}

.header h1 {
  font-size: 36px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.header h2 {
  font-size: 20px;
  font-style: italic;
}

.header p {
  font-size: 14px;
  margin-top: 0;
}

/* Chord Chart Section */
.chord-section {
  grid-area: chord-chart / chord-text;
  display: grid;
  grid-template-columns: 2fr 3fr;
  gap: 15px;
}

.chord-chart {
  grid-column: 1;
}

.chord-text {
  grid-column: 2;
  align-self: center;
  font-size: 14px;
  line-height: 1.6;
}

/* Radar Chart Section */
.radar-section {
  grid-area: radar-chart;
  display: flex;
  justify-content: center;
}

.radar-chart {
  width: 100%;
}

/* Scatterplot Section */
.scatter-section {
  grid-area: scatter-chart;
  display: flex;
  justify-content: center;
}

.scatter-chart {
  width: 100%;
}

/* Text Section */
.text-section {
  grid-area: text;
  font-size: 14px;
  line-height: 1.6;
}

/* Line Chart Section */
.line-section {
  grid-area: line-chart;
  margin-top: 10px;
}

/* Footer */
.footer {
  grid-area: footer;
  text-align: center;
  font-size: 12px;
  border-top: 1px solid #ccc;
  padding-top: 10px;
  margin-top: 20px;
}
